<template>
  <div class="wrapper">
    <div v-if="sloading">
      Loading
    </div>
    <div class="home" v-if="sready || schanged || sawait">
      <div class="value">{{ethValue}}</div>
      <div class="changeform">
        <input type="number" v-model="settedValue" style="{width: 30px;}">
        <input type="button" @click="setVal(settedValue)" value="Сохранить">
      </div>
      <div class="backstop_wrapper" v-if="sawait">
        <div class="backstop">Await</div>
      </div>
      <div v-if="message">{{ message }}</div>
    </div>
    <div v-if="serror" class="message">Ошибка</div>
  </div>
</template>

<script>
// @ is an alias to /src
// import Vue from 'vue'
const STATUSES = {
  LOADING: 0,
  READY: 1,
  CHANGED: 2,
  AWAIT: 3,
  ERROR: -1
}

export default {
  name: 'Home',
  components: {},
  data(){
    return {
      ethValue: 0,
      settedValue: 0,
      message: "",
      status: STATUSES.LOADING
    }
  },
  computed: {
    sloading() {return this.status === STATUSES.LOADING},
    sready() {return this.status === STATUSES.READY},
    schanged() {return this.status === STATUSES.CHANGED},
    sawait() {return this.status === STATUSES.AWAIT},
    serror() {return this.status === STATUSES.ERROR},
  },
  async mounted() {
    this.settedValue = this.ethValue = await this.$web3.getValue()
    this.status = STATUSES.READY
  },
  methods: {
    async setVal(val) {
      this.status = STATUSES.AWAIT
      await this.$web3.setValue(val).then(
          ()=>{this.showMessage("saved")},
          (error)=>{this.showMessage("ERROR " + error.message)}
          )
      this.status = STATUSES.READY
    },
    showMessage(text){
      this.message = text
      setTimeout(()=> this.message = "", 3000)
    }
  }

}
</script>
<style>
.wrapper{
  width: 200px;
  margin: 10vh auto;
}
.backstop_wrapper{
  width: 100vw;
  height: 100vh;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgb(12 255 250 / 20%);
}
.backstop{
  position: absolute;
  display: block;
  top: 10vh;
  left: 50%;
  transform: translate(-50%);
  width: 50px;
  background: aqua;
  padding: 30px;
  border-radius: 50%;
}
.message{
  position: absolute;
  top: 2vh;
  left: 50%;
  transform: translate(-50%);
  background: antiquewhite;
  padding: 11px 54px;
  border-radius: 30px;
}
</style>
