import Web3 from 'web3';
import UsContract from '@/assets/api.json'

const address = '0x8E5cD7894a5FD481a5f19537bC14E427332cA431'

export default {
  async install() {
    const web3 = new Web3(window.ethereum)

    await window.ethereum.enable();

    const contract = new web3.eth.Contract(UsContract,address);
    // console.log(webType); == kovan

    const accountAddr = await web3.eth.getAccounts();

    const setValue = async (val) => {
      const webType = await web3.eth.net.getNetworkType();
      console.log(webType)
      if(webType !== "kovan") {
        throw new Error("Not correct web. Need kovan, but have "+webType);
      }
      const gas = await contract.methods.setValue(val).estimateGas({from: accountAddr[0]})
      console.log(gas);
      return contract.methods.setValue(val).send({from: accountAddr[0], gas: gas})
    }

    const getValue = async () => {
      return await contract.methods.getValue().call({from: accountAddr[0]});
    }

    return {setValue, getValue}
  }
}

